<template>
  <div id="LiveTypeList">
    <head-top :title="nowLiveLession.mark || ''"></head-top>
    <div v-if="nowLiveLession" class="main">
      <van-tabs v-model="selectedTab">
        <van-tab title="老师实况">
          <div v-if="nowLiveLession.teachers">
            <div v-for="(item, index) in nowLiveLession.teachers" :key="index" class="line_mark"
              @click="select(item, index)">
              <img :src="defaultPost" alt="" />
              <div class="line_right">
                <div class="line_info">{{ item.title }}</div>
                <van-tag mark type="success">{{ item.typeName }}</van-tag>
                <div>{{ item.time | formatDate(item.time) }}</div>
              </div>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-tab>
        <van-tab title="小组实况">
          <div v-if="nowLiveLession.students">
            <div v-for="(item, index) in nowLiveLession.students" :key="index" class="line_mark"
              @click="select(item, index)">
              <img :src="defaultPost" alt="" />
              <div class="line_right">
                <div class="line_info">{{ item.title }}</div>
                <van-tag mark type="success">{{ item.typeName }}</van-tag>
                <div>{{ item.time | formatDate(item.time) }}</div>
              </div>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-tab>
      </van-tabs>
    </div>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>
import defaultPost from "@/assets/video/videopost.png";

export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      selectedTab: 0,
      defaultPost
    };
  },
  computed: {
    nowLiveLession() {
      return this.$store.getters.liveLession;
    }
  },
  methods: {
    select(item, index) {
      this.$router.push("/livePlayback/" + this.selectedTab + "/" + index);
    }
  }
};
</script>

<style lang="less">
@import url("./../../styles/common/base.less");
#LiveTypeList {
  width: 100%;
  height: 100%;
  background-color: #fff;

  .main {
    width: 100%;
    background: #ffffff;
    max-height: calc(100vh - 45px);
    overflow-y: auto;
    .line_mark {
      position: relative;
      display: flex;
      justify-content: start;
      margin: 0 3vw;
      font-size: @font-info;
      line-height: 1.5;
      color: #777;
      padding: 2vw 0;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid #f0f1f1;
        transform: scaleY(0.6);
      }

      img {
        max-width: 40vw;
        max-height: 26vw;
        padding-right: 4vw;
      }
      .line_right {
        text-align: left;
        word-break: break-all;
        .line_info {
          height: 11vw;
        }
      }
    }
  }
}
</style>



